<template>
  <ul v-if="pokemons && pokemons.length" class="sidebar">
    <li v-for="(pokemon, index) in pokemons" :key="pokemon.id" v-on:click="handleClick(index)">
      {{pokemon.name}}
    </li>
  </ul>
  <div v-if="show">
    <div>Name: {{pokemonSelected.name}}</div>
    <img :src="pokemonSelected.sprites.front_shiny">
    <div>Type: {{pokemonSelected.types[0].type.name}}</div>
    <div>Weight: {{pokemonSelected.weight}}</div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      show: false,
      message: 'Hello Vue!',
      pokemons: [],
      pokemonSelected: {}
    }
  },
  mounted () {
    axios.get('https://pokeapi.co/api/v2/pokemon?limit=20&offset=0')
        .then(response => {
          this.pokemons = response.data.results
          console.log(response.data.results)
        })
  },
  methods: {
    handleClick(index) {
      const pokemonSelected = this.pokemons[index];

      axios.get(pokemonSelected.url)
          .then(response => {
            this.show = true;
            this.pokemonSelected = response.data;
            console.log(this.pokemonSelected)
          })
    }
  }
}
</script>

